@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;700&display=swap);
* {
  padding: 0;
  text-decoration: none;
  list-style: none;
  box-sizing: border-box;
  font-family: "Roboto", sans-serif;
}

p {
  font-size: 16px;
}

input[type="text"] {
  height: 50px;
  font-size: 16px;
  outline: none;
  border: 1px solid #ccc;
  background-color: #fcfcfc;
  border-radius: 5px;
  padding: 0 10px;
}

.root {
  width: 100%;
  min-height: 100vh;
}

.option-button {
  width: calc(50% - 10px);
  height: 115px;
  background-color: transparent;
  border: none;
  outline: none;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
}

.option-button:hover {
  background-color: #f5f5f5;
}

.option-button label {
  font-size: 18px;
  font-weight: 400;
}

.page-title {
  font-weight: bold;
  text-align: center;
  margin: 50px 0;
}

.separator {
  width: 1px;
  height: 115px;
  background-color: #f5f5f5;
}

header {
  width: 100%;
  height: 70px;
  box-shadow: 0px 4px 4px rgba(216, 216, 216, 0.25);
}

.input-group {
  display: flex;
  flex-direction: column;
  margin-bottom: 15px;
}

.input-group label {
  font-weight: lighter;
  font-size: 13px;
